.wave-animation {
  display: flex;
  align-items: center;
  justify-content: center;
}

#Line_1 {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  -webkit-animation-delay: calc(1 * 0.15s);
          animation-delay: calc(1 * 0.15s);
}

#Line_2 {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  -webkit-animation-delay: calc(2 * 0.15s);
          animation-delay: calc(2 * 0.15s);
}

#Line_3 {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  -webkit-animation-delay: calc(3 * 0.15s);
          animation-delay: calc(3 * 0.15s);
}

#Line_4 {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  -webkit-animation-delay: calc(4 * 0.15s);
          animation-delay: calc(4 * 0.15s);
}

#Line_5 {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  -webkit-animation-delay: calc(5 * 0.15s);
          animation-delay: calc(5 * 0.15s);
}

#Line_6 {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  -webkit-animation-delay: calc(6 * 0.15s);
          animation-delay: calc(6 * 0.15s);
}

#Line_7 {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  -webkit-animation-delay: calc(7 * 0.15s);
          animation-delay: calc(7 * 0.15s);
}

#Line_8 {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  -webkit-animation-delay: calc(8 * 0.15s);
          animation-delay: calc(8 * 0.15s);
}

#Line_9 {
  -webkit-animation: pulse 1s;
          animation: pulse 1s;
  -webkit-animation-delay: calc(9 * 0.15s);
          animation-delay: calc(9 * 0.15s);
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }

  50% {
    -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }

  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
}

@keyframes pulse {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }

  50% {
    -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }

  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
  }
}

